@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;

}

.appContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #F5F5F5;
}

.app{
  width: 400px;
  height: 600px;
  border: 1px solid #B5B5B5;
  border-radius: 10px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.textInput{
  width: 25%;
  height: 40px;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 0 10px;
  font-size: 16px;
  margin-bottom: 10px;
  outline: none;
}

.textInput:focus{
  border: 1px solid #888;
}

.checkboxContainer{
  display: flex;
  align-items:  start ;
  margin-bottom: 10px;
  justify-content: center;
  width: 150px;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 15px;
  
}

.checkbox{
  border: 1px solid #B5B5B5;
  min-width: 40px;
  height: 22px;
  border-radius: 25px;
  display: flex;
  align-items: center;
  padding: 2px;
  cursor: pointer;
}

.switchOff{
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #B5B5B5;
  transition: all 0.3s ease;
}
.switchOn{
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #348DD3;
  transition: all 0.3s ease;
  transform: translateX(20px);
}

.switchContainer{
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 10px;
  width: 150px;
}

.Button{
  width: 30%;
  height: 40px;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 0 10px;
  font-size: 16px;
  margin-bottom: 10px;
  outline: none;
  background-color: #348DD3;
  color: white;
  cursor: pointer;
}

.button:hover{
  background-color: #3d729b;
  color: white;
}